<template>
  <div class="welcome">
      <form @submit.prevent="enterChat">
        <input class="inputName" type="text" placeholder="Enter Your Name" v-model="name" autocomplete="off">
        <div v-if="feedback" class="feedback">{{ feedback }}</div>
        <button class="enterChat">시작하기</button>
      </form>
  </div>
</template>

<script>
export default {
  name: 'Welcome',
  data () {
    return {
      name: null,
      feedback: '6글자 이하로 입력해주세요..'
    }
  },
  methods: {
    enterChat(){
      if(this.name){
        this.$router.push({ name: 'Chat', params: { name: this.name } })
      } else {
        this.feedback = '닉네임이 있어야 합니다..'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.welcome{
  width:100vw;
  height:100vh;
  background: #FCCE00;
	font-family:'Kakao';
  text-align:center;
  display:flex;
  align-items:center;
  flex-direction: column;
  justify-content: space-around;
}

@media (min-width: 450px) {
.welcome{
  width:330px;
  height:600px;
  background: #FCCE00;
	font-family:'Kakao';
  text-align:center;
  display:flex;
  align-items:center;
  flex-direction: column;
  justify-content: space-around;
}
}

.welcome input{
	font-family:'Kakao';
  color:#333;
  background: transparent;
  border: none;
  border-bottom: 1px solid #777;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 80%;
  font-size:1.2em;
  margin: 0 0 8px 0;
  padding: 0;
  box-sizing: content-box;
  transition: box-shadow .3s, border .3s, -webkit-box-shadow .3s;
  text-align:center;
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #777;
    opacity: 1; /* Firefox */
}
.welcome button{
  color: #FCCE00;
  background: #333;
  border: none;
  border-radius: 2px;
  display: block;
  height: 36px;
  line-height: 36px;
  padding: 0 16px;
  text-transform: uppercase;
  -webkit-tap-highlight-color: transparent;
  margin: 30px auto;
}
.welcome .feedback{
  color: red;
}
</style>



