<template>
    <div class="chat" id="chat">
        <div class="readMessage" id="readMessage">
            <ul class="messages" id="messages">
                <ul class="from_you" v-for="message in messages" :key="message.id">
                <li>                        
                    <span class="nm">{{ message.name }}</span>                        
                    <span class="co" id="messagebox">{{ message.content }}</span>                       
                    <span class="tm">{{ message.timestamp }}</span>
                    
                </li>
                </ul>
            </ul>
        </div>
        <div class="sendMessage">
            <NewMessage :name="name" />
        </div>
    </div>
</template>

<script>
import NewMessage from '@/components/NewMessage'
import db from '@/firebase/init'
import moment from 'moment'

export default {
    name: 'Chat',
    props: ['name'],
    components: {
        NewMessage
    },
    data(){
        return {
            messages: []
        }
    },
    created(){
        let ref = db.collection('messages').orderBy('timestamp')

        ref.onSnapshot(snapshot => {
            snapshot.docChanges().forEach(change => {
                if(change.type == 'added'){
                    let doc = change.doc
                    this.messages.push({
                        id: doc.id,
                        name: doc.data().name,
                        content: doc.data().content,
                        timestamp: moment(doc.data().timestamp).format('LT')
                    })
                }
            })
        })
        
        var el = document.getElementById('messages'); 
        el.scrollTop = el.scrollHeight;
    },
    updated(){
        var el = document.getElementById('messages'); 
        el.scrollTop = el.scrollHeight;        
    }
}
</script>

<style>
.chat{
    font-family: 'kakao';
	width: 100vw;
	background-color:#a1c0d6;
}

.readMessage{
    height: 93vh;
    padding-left: 10px;
}

.sendMessage{
    height: 7vh;
    background: #fff;
}

@media (min-width: 450px) {
    .chat{
        font-family: 'kakao';
        width: 330px;
        background-color:#a1c0d6;
    }
    .readMessage{
        height: 550px;
        padding-left:10px;
    }
    .sendMessage{
        height: 50px;
        background: #fff;
    }
}

.chat > ul{
	position: relative;
	width: 330px;
	z-index: 0;
}
ul.from_me {
	text-align: right;
}
ul.from_me > li {
    display: inline-block;
	text-align: left;
    position: relative;
    background: #FCCE00;
	word-wrap: break-word;
    min-width:30%;
	max-width: 66%;
	padding:10px;
	margin-top: 10px;
	margin-bottom: 30px;
    border-radius: 10px;
}
ul.from_me > li:after {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #a1c0d6;
    bottom: -10px;
    right: -13px;
}
ul.from_me > li:before {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #FCCE00;
    bottom: -6px;
    right: -4px;
}

ul.from_me > li > span.nm {
	position: absolute;
	right:0px;
	top:-20px;
    max-width:100%;
}

ul.from_me > li > span.tm {
	position: absolute;
	font-size: 14px;
	left:-55px;
	bottom:0px;
}
ul.from_you > li {
    display: inline-block;
    position: relative;    
	background: #FFFFFD;	
	word-wrap: break-word;
    min-width:20%;
	max-width: 66%;
	padding:10px;
	margin-top: 10px;
	margin-bottom: 30px;
    border-radius: 10px;
}
ul.from_you > li:after {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #a1c0d6;
    bottom: -10px;
    left: -13px;
}
ul.from_you > li:before {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #FFFFFD;
    bottom: -6px;
    left: -4px;
}
ul.from_you {
	text-align: left;
}

ul.from_you > li > span.nm {
	position: absolute;
	left:0px;
	top:-20px;
}

ul.from_you > li > span.tm {
	position: absolute;
	font-size: 14px;
	right:-58px;
	bottom:0px;
}

.messages{
    width:95vw;
    max-height: 93vh;
    overflow: auto;
}

@media (min-width: 450px) {
.messages{
    width:330px;
    max-height: 558px;
    overflow: auto;
}
}

/* width */
::-webkit-scrollbar {
    width: 0px;
}
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
</style>



