<template>
    <div class="new-message">
        <form @submit.prevent="addMessage">
            <input type="text" name="new-message" v-model="newMessage" autocomplete="off">            
            <button class="sendButton" @submit.prevent="sendMessage">전송</button>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'NewMessage',
    props: ['name'],
    data(){
        return{
            newMessage: null,
            feedback: null
        }
    },
    methods: {
        addMessage(){
            if(this.newMessage){
                db.collection('messages').add({
                    content: this.newMessage,
                    name: this.name,
                    timestamp: Date.now()
                }).catch(err => {
                    console.log(err)
                })
                this.newMessage = null
                this.feedback = null
            } else {
                this.feedback = "You must enter a message in order to send one"
            }
        },
        sendMessage(){
            this.addMessage()
        }
    }
}
</script>

<style>
.new-message{
    width: 95vw;
}

@media (min-width: 450px) {
.new-message{
    width: 330px;
}
}
.new-message form{
    display:flex;
    padding-left: 5%;
}

.new-message button{
    margin:auto;
}

.new-message input{
    font-family:'Kakao';
    color:#333;
    background: transparent;
    border: none;
    border-bottom: 1px solid #777;
    border-radius: 0;
    outline: none;
    height: 3rem;
    width: 85%;
    font-size:1.2em;
    margin: 0 0 8px 0;
    box-sizing: content-box;
    transition: box-shadow .3s, border .3s, -webkit-box-shadow .3s;
}

.new-message button{
    font-family: 'kakao';
    font-weight: 700;
    color: #333;
    background: #FCCE00;
    border: none;
    border-radius: 2px;
    height: 36px;
    width: 20%;
    line-height: 36px;
    text-transform: uppercase;
    -webkit-tap-highlight-color: transparent;
    margin-left: 10px;
}
</style>


